@keyframes blur {
  from {
    filter: blur(20px);
  }

  to {
    filter: blur(0);
  }
}

@keyframes blur-library {
  from {
    filter: blur(20px);
  }

  to {
    filter: blur(0);
  }
}

@keyframes opacity {
  from {
    opacity: 0;
    transform: scale(0.7);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}