}

:root {
  --accent: #ff6b08;
  --main-bgd-color: #e5e5e5;
  --bgd-accent: #f1f1f1;
  --main-text-color: #000;
  --error: #ff001b;
  --deepdark-text-color: #545454;
  --light-text-color: #fff;
  --transition: 250ms linear;
}

html {
  scroll-behavior: smooth;
}

body {
