.ldld {
  visibility: hidden;
  opacity: 0;
  transition: all .3s ease-in-out;
}

.ldld.running {
  visibility: visible;
  opacity: 1;
}

.ldld.dark {
  color: rgba(0, 0, 0, .5);
}

.ldld.light {
  color: rgba(255, 255, 255, .5);
}

.ldld.default:before, .ldld.full:before, .ldld.ldbtn:before, .ldld.bare:before {
  content: " ";
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background: 0;
  border: 2px solid;
  border-color: currentColor rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 0;
  animation: ldld-default .5s ease-in-out infinite;
  display: block;
}

.ldld.default {
  z-index: 1;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

